<template>
  <v-container>
    <v-alert
      prominent
      type="success"
      v-if="meetings && new Date(meetings.datetime).getTime() > Date.now()"
    >
      <v-row align="center">
        <v-col class="grow">
          You have an upcoming meeting on
          {{
            new Date(meetings.datetime).toDateString() +
            " " +
            new Date(meetings.datetime).toLocaleTimeString()
          }}
        </v-col>
        <v-col class="shrink">
          <v-btn to="/videomeetings">view details</v-btn>
        </v-col>
      </v-row>
    </v-alert>
    <h2 class="text-center my-3 hidden-md-and-up">Daily Report</h2>
    <div class="mt-5">
      <v-sheet max-width="400" class="mx-auto transparent">
        <v-text-field
          type="month"
          outlined
          dense
          label="Select Month"
          v-model="month"
          @change="filterByMonth"
        ></v-text-field>
        <v-row class="mb-2" justify="center">
          <v-btn class="indigo mx-1" large dark>apply</v-btn>
          <v-btn
            class="indigo mx-1 hidden-md-and-up"
            dark
            @click="charts = !charts"
            large
            >charts</v-btn
          >
        </v-row>
      </v-sheet>
    </div>
    <v-card v-if="dailydata.length > 0">
      <v-card-text>
        <v-row>
          <v-col cols="12" md="6">
            <div
              v-for="(d, i) in dailydata"
              :key="i"
              class="text-center my-5 report montaserrat"
            >
              <div class="report--top">
                <h2 class="mt-5 indigo--text">
                  {{ new Date(d.date).toDateString() }}
                </h2>
                <v-btn
                  @click="editReport(d)"
                  fab
                  small
                  class="indigo mt-3 mr-8"
                  dark
                >
                  <v-icon>mdi-pencil</v-icon>
                </v-btn>
              </div>
              <div class="report--bottom">
                <div>
                  <h2>Hours</h2>
                  <h3>{{ d.hour }}</h3>
                </div>
                <div>
                  <h2>Page</h2>
                  <h3>{{ d.page }}</h3>
                </div>
                <div>
                  <h2>Efficiency</h2>
                  <h3>{{ Number(d.efficiency).toFixed(2) }}</h3>
                </div>
              </div>
            </div>
          </v-col>
          <v-col cols="12" md="6" class="hidden-sm-and-down">
            <study-chart :dailydata="dailydata"></study-chart>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    <div class="text-center" v-else>
      <p class="mx-auto">No data found.</p>
    </div>
    <v-dialog max-width="600" v-model="submitted">
      <v-card class="pa-5">
        <v-card-title>
          <h2
            class="mx-auto text-center indigo white--text pa-2"
            style="border-radius: 5px"
          >
            Report Details
          </h2>
        </v-card-title>
        <v-card-text>
          <v-simple-table class="boxed">
            <tbody>
              <tr>
                <td>Date:</td>
                <td>{{ new Date().toDateString() }}</td>
              </tr>
              <tr>
                <td>Hours:</td>
                <td>
                  <v-text-field
                    label="Hours"
                    type="number"
                    v-model="dayData.hour"
                  />
                </td>
              </tr>
              <tr>
                <td>Pages:</td>
                <td>
                  <v-text-field
                    placeholder="Pages"
                    type="number"
                    v-model="dayData.page"
                  />
                </td>
              </tr>
            </tbody>
          </v-simple-table>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn fab dark class="orange" @click="submitted = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-btn fab dark @click="addDate()" color="indigo" :loading="loading">
            <v-icon>mdi-check</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="charts" max-width="600">
      <v-card>
        <v-card-text>
          <study-chart :dailydata="dailydata"></study-chart>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { auth } from "@/plugins/firebase";
import StudyChart from "@/components/StudyChart";
export default {
  components: { StudyChart },
  async beforeRouteEnter(to, from, next) {
    console.log("ok");
    const res = await fetch(
      `https://script.google.com/macros/s/AKfycbw_kdcD4HYnkByFmpIN-zUYWLeQmoqMiXcAIPc_IVtVs4h0p3NN77U18jFtwKB8TWMknw/exec?sheet=${auth.currentUser.photoURL}&type=dailydata`
    );
    const data = await res.json();
    let dp = await data.routines
      .filter((r) => r.date)
      .sort((a, b) => b.id - a.id);
    let ddata = await dp.filter(
      (d) => new Date(d.date).getMonth() == new Date().getMonth()
    );

    next((vm) => {
      vm.dailydata = ddata;
      vm.alldays = dp;
    });
  },
  data() {
    return {
      charts: false,
      month: `${new Date().getFullYear()}-0${new Date().getMonth() + 1}`,
      dailydata: [],
      dayData: {
        hour: 0,
        page: 0,
      },
      submitted: false,
      loading: false,
      alldays: [],
      meetings: [],
    };
  },

  methods: {
    filterByMonth() {
      this.dailydata = this.alldays.filter(
        (d) => new Date(d.date).getMonth() == new Date(this.month).getMonth()
      );
    },
    editReport(d) {
      this.dayData = d;
      this.submitted = true;
    },
    getData() {
      fetch(
        "https://script.google.com/macros/s/AKfycbxZwegkJojM8GImNcevhXJpbUBiwzCFFN2sn6TICkoOTEmPl6mV_OIlaIJlfdm-HL-RYA/exec?roll=" +
          this.user.displayName
      )
        .then((res) => res.json())
        .then((data) => {
          this.meetings = data.routines.sort(
            (a, b) => b.meeting_id - a.meeting_id
          )[0];
        });
    },
    addDate() {
      this.loading = true;
      fetch(
        "https://script.google.com/macros/s/AKfycbw_kdcD4HYnkByFmpIN-zUYWLeQmoqMiXcAIPc_IVtVs4h0p3NN77U18jFtwKB8TWMknw/exec",
        {
          method: "POST",
          mode: "no-cors",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify({
            sheetid: this.user.photoURL,
            ...this.dayData,
          }),
        }
      ).then(() => {
        let index = this.dailydata.findIndex((s) => s.id == this.dayData.id);

        this.dailydata[index] = {
          ...this.dayData,
          efficiency: this.dayData.page / this.dayData.hour,
        };
        this.loading = false;
        this.submitted = false;
      });
    },
  },
  computed: {
    user() {
      return this.$store.getters.getUser;
    },
  },
  created() {
    this.getData();
  },
};
</script>

<style lang="scss" scoped>
.any {
  color: rgb(9, 145, 199);
}

.report {
  border-radius: 5px;
  border: 1px solid #3f51b5 !important;
  &--top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    padding: 0 20px;
  }

  &--bottom {
    display: flex;
    justify-content: space-between;
    background-image: linear-gradient(
      to top right,
      #3f51b5,
      #4558c0,
      #4c5fca,
      #5266d5,
      #586de0
    );

    & > div {
      color: white;
      padding: 10px 15px;
    }
    & > div:last-child {
      background-image: linear-gradient(
        to right top,
        #ffc542,
        #fccc38,
        #f8d32d,
        #f2da21,
        #ebe212
      );
      color: #3f51b5;
    }
  }
}
.boxed {
  border-radius: 10px;
  padding: 10px 15px;
  background: linear-gradient(145deg, #ffffff, #eee);
  box-shadow: 20px 20px 60px #cccad2, -20px -20px 60px #ffffff;
}
</style>