<template>
  <div class="pt-4">
    <div class="border_blue">
      <h1 class="text-center my-3">Hour/Day</h1>
      <column-chart
        adapter="chartjs"
        ytitle="Hour"
        xtitle="Date"
        :download="{ background: '#fff' }"
        :colors="['#6f5fdb', '#f4943d']"
        :data="
          dailydata.map((d) => [new Date(d.date).toLocaleDateString(), d.hour])
        "
      ></column-chart>
    </div>
    <div class="border_orange my-4">
      <h1 class="text-center my-3">Page/Day</h1>
      <column-chart
        ytitle="Page"
        xtitle="Date"
        :download="{ background: '#fff' }"
        :colors="['#f4943d', '#6f5fdb']"
        :data="
          dailydata.map((d) => [new Date(d.date).toLocaleDateString(), d.page])
        "
      ></column-chart>
    </div>
    <div class="border_blue">
      <h1 class="text-center my-3">Efficiency/Day</h1>
      <line-chart
        :download="{ background: '#fff' }"
        ytitle="Efficiency"
        xtitle="Date"
        :colors="['#6f5fdb']"
        :data="
          dailydata.map((d) => [
            new Date(d.date).toLocaleDateString(),
            d.efficiency,
          ])
        "
      ></line-chart>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    dailydata: {
      type: Array,
    },
  },
};
</script>

